<template>
  <div class="dropdown-w w-dropdown" v-if="options">

    <!-- Dropdown Input -->
    <div
      class="state_dropdown-trigger"
      :class="optionsShown ? 'w--open': ''"
    >
      <input class="dropdown"
        autocomplete="off"
        :name="name"
        @focus="showOptions()"
        @blur="exit()"
        @keyup="keyMonitor"
        v-model="searchFilter"
        :disabled="disabled"
        :placeholder="placeholder" />

      <i v-show="!optionsShown" class="fa fa-angle-down" style="margin-left: -2rem;"></i>
      <i v-show="optionsShown" class="fa fa-angle-up" style="margin-left: -2rem;"></i>
    </div>

    <!-- Dropdown Menu -->
    <nav
      class="dropdown-list w-dropdown-list w--open"
      v-show="optionsShown && filteredOptions && filteredOptions[0]"
      ref="scrollContainer"
    >
      <div
        v-for="(option, index) in filteredOptions"
        :key="index"
        class="dropdown-link w-dropdown-link"
        :class="{ 'is--template-border': index === arrowCounter }"
        @mousedown="selectOption(option)"
        ref="options"
      >
        {{ option.name || option.id || '-' }}
      </div>
    </nav>
  </div>
</template>

<script>
  export default {
    name: 'Dropdown',
    template: 'Dropdown',
    props: {
      name: {
        type: String,
        required: false,
        default: 'dropdown',
        note: 'Input name'
      },
      options: {
        type: Array,
        required: true,
        default: () => [],
        note: 'Options of dropdown. An array of options with id and name',
      },
      placeholder: {
        type: String,
        required: false,
        default: 'Please select an option',
        note: 'Placeholder of dropdown'
      },
      disabled: {
        type: Boolean,
        required: false,
        default: false,
        note: 'Disable the dropdown'
      },
      maxItem: {
        type: Number,
        required: false,
        default: 6,
        note: 'Max items showing'
      },
      selectedOption: {
        type: Object,
        required: false,
        default: () => {},
        note: 'Selected item'
      }
    },
    data() {
      return {
        selected: {},
        optionsShown: false,
        searchFilter: '',
        arrowCounter: 0,
      }
    },
    created() {
      this.$emit('selected', this.selected);
      if (this.selectedOption && this.selectedOption.id) {
        this.searchFilter = this.selectedOption.name;
        this.selected = this.selectedOption;
        this.$emit('selected', this.selected);
      }
    },
    computed: {
      filteredOptions() {
        const filtered = [];
        const regOption = new RegExp(this.searchFilter, 'ig');
        for (const option of this.options) {
          if (this.searchFilter.length < 1 || option.name.match(regOption)){
            if (filtered.length < this.maxItem) filtered.push(option);
          }
        }
        return filtered;
      }
    },
    methods: {
      selectOption(option) {
        this.selected = option;
        this.optionsShown = false;
        this.searchFilter = this.selected.name;
        this.$emit('selected', this.selected);
      },
      showOptions(){
        if (!this.disabled) {
          this.searchFilter = '';
          this.optionsShown = true;
        }
      },
      exit() {
        if (!this.selected.id) {
          this.selected = {};
          this.searchFilter = '';
        } else {
          this.searchFilter = this.selected.name;
        }
        this.$emit('selected', this.selected);
        this.optionsShown = false;
      },
      // Selecting when pressing Enter
      keyMonitor: function(event) {
        if (event.key === "Enter" && this.filteredOptions[0]) {
          this.selectOption(this.filteredOptions[this.arrowCounter]);
        }
        if (event.key === "ArrowUp" && this.filteredOptions[0]) {
          event.preventDefault()
          if (this.arrowCounter > 0) {
            this.arrowCounter = this.arrowCounter - 1;
            this.fixScrolling();
          }
        }
        if (event.key === "ArrowDown" && this.filteredOptions[0]) {
          event.preventDefault()
          if (this.arrowCounter < this.filteredOptions.length - 1) {
            this.arrowCounter = this.arrowCounter + 1;
            this.fixScrolling();
          }
        }
      },
      fixScrolling(){
        const liH = this.$refs.options[this.arrowCounter].clientHeight;
        this.$refs.scrollContainer.scrollTop = liH * this.arrowCounter;
      },
    },
    watch: {
      searchFilter() {
        if (this.filteredOptions.length === 0) {
          this.selected = {};
        }
        this.$emit('filter', this.searchFilter);
      },
      selectedOption() {
        if (this.selectedOption && this.selectedOption.id) {
          this.searchFilter = this.selectedOption.name;
          this.selected = this.selectedOption;
          this.$emit('selected', this.selected);
        }
      }
    }
  };
</script>


